.key {
	padding-top: 118px;
	padding-bottom: 128px;
	position: relative;
	@include r(1920) {
		padding-top: 100px;
		padding-bottom: 100px; }
	@include r(1300) {
		padding-top: 68px;
		padding-bottom: 68px; }
	@include r(768) {
		padding: 60px 0; }
	@include r(479) {
		padding-bottom: 10px;
		padding-top: 40px; }
	.fico {
		&-starB, &-starS {
			position: absolute; }
		&-starB {
			&--1 {
				left: calc(50% - 937px);
				top: 146px; }
			&--2 {
				left: calc(50% - 360px);
				top: 288px; }
			&--3 {
				left: calc(50% - -950px);
				top: 320px; } }
		&-starS {
			&--1 {
				left: calc(50% - 1137px);
				top: 727px; }
			&--2 {
				left: calc(50% - 954px);
				top: 502px; }
			&--3 {
				left: calc(50% - -178px);
				top: 269px; }
			&--4 {
				right: calc(50% - 224px);
				top: 582px; } } }
	&__title {
		text-align: center;
		&--small {
			margin-bottom: 97px;
			@include r(1920) {
				margin-bottom: 82px; }
			@include r(1300) {
				margin-bottom: 64px; }
			@include r(768) {
				margin-bottom: 50px; }
			@include r(640) {
				margin-bottom: 40px; }
			@include r(479) {
				margin-bottom: 20px; } }
		&--main {
			text-align: left;
			margin-bottom: 38px;
			line-height: 55px;
			@include r(1300) {
				margin-bottom: 22px; }
			@include r(768) {
				text-align: center; }
			@include r(479) {
				margin-bottom: 14px; } } }
	&__line {
		text-align: center;
		position: relative;
		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 0;
			right: 0;
			transform: translateY(-50%);
			height: 1px;
			background: transparentize($purple, .8);
			z-index: -1; } }
	&__img {
		padding: 0 33px;
		background-color: $white;
		@include r(768) {
			width: 170px; }
		@include r(479) {
			width: 84px;
			padding: 0 10px; } }
	&__wrap {
		display: flex;
		align-items: flex-start;
		margin-bottom: 74px;
		margin-top: 93px;
		@include r(1920) {
			margin-top: 78px;
			margin-bottom: 74px; }
		@include r(1300) {
			margin-top: 34px;
			margin-bottom: 65px;
			justify-content: space-between; }
		@include r(768) {
			flex-direction: column;
			margin-bottom: 50px; }
		@include r(479) {
			margin-top: 11px;
			margin-bottom: 40px; } }
	&__left {
		max-width: 750px;
		width: 100%;
		margin-right: 30px;
		flex-shrink: 0;
		@include r(1920) {
			max-width: 570px; }
		@include r(1300) {
			max-width: 470px;
			margin-right: 20px;
			margin-top: 20px; }
		@include r(1024) {
			max-width: 48.8%; }
		@include r(768) {
			max-width: 100%;
			margin-top: 0;
			margin-right: 0;
			margin-bottom: 25px; } }
	&__desc {
		max-width: 94%;
		@include r(479) {
			max-width: 100%; } }
	&__text {
		@include nlt {
			margin-bottom: 30px; }
		@include r(1300) {
			line-height: 25px;
			@include nlt {
				margin-bottom: 23px; } }
		@include r(768) {
			@include nlt {
				margin-bottom: 21px; } }
		@include r(479) {
			font-size: 16px; } }
	&__right {
		display: flex;
		align-items: flex-start;
		@include r(1024) {
			flex: 1; }
		@include r(768) {
			width: 100%; }
		@include r(479) {
			flex-direction: column; } }
	&__btn {
		margin: auto; } }


.keys {
	width: 390px;
	border-radius: 10px;
	padding: 23px 31px 39px;
	&--white {
		background-color: $white;
		box-shadow: 0px 35px 60px rgba(54, 44, 98, 0.2);
		margin-left: -32px;
		.keys__top {
			justify-content: space-between; } }
	&--colored {
		background-color: #F9F5EA;
		margin-top: 80px;
		.keys__ttl {
			margin-right: 49px; } }
	@include r(1920) {
		width: 300px;
		padding-bottom: 24px; }
	@include r(1300) {
		width: 246px;
		padding: 14px 15px 23px 19px;
		&--white {
			margin-left: -22px; }
		&--colored {
			margin-top: 50px;
			padding-right: 33px;
			.keys__ttl {
				margin-right: 26px; } } }
	@include r(1024) {
		width: calc(100%/2 + 30px);
		&--colored {
			.keys__ttl {
				margin-right: 0; }
			.keys__top {
				justify-content: space-between; } } }
	@include r(479) {
		width: 100%;
		padding: 20px 14px 25px 20px;

		&--white {
			margin-left: 0;
			margin-top: -10px; }
		&--colored {
			margin-top: 0;
			padding-right: 14px;
			padding-bottom: 35px; } }
	.fico {
		color: $purple;
		font-size: 33px;
		@include r(1024) {
			font-size: 32px; }
		@include r(900) {
			font-size: 30px; } }
	&__top {
		display: flex;
		align-items: center;
		margin-bottom: 25px;
		@include r(1920) {
			margin-bottom: 18px; }
		@include r(1300) {
			margin-bottom: 10px; }
		@include r(479) {
			margin-bottom: 19px; } }
	&__ttl {
		color: $dark-blue;
		@include r(479) {
			font-size: 25px; } }
	.list {
		&__item {
			color: $dark-blue;
			line-height: 20px;
			@include nlt {
				padding-bottom: 15px;
				margin-bottom: 14px;
				border-bottom: 1px solid transparentize($dark-blue, .9); }
			@include r(1920) {
				font-size: 16px;
				@include nlt {
					padding-bottom: 10px;
					margin-bottom: 9px; } }
			@include r(1300) {
				font-size: 15px;
				line-height: 18px;
				@include nlt {
					padding-bottom: 7px;
					margin-bottom: 7px; } }
			@include r(479) {
				@include nlt {
					padding-bottom: 11px;
					margin-bottom: 10px; } } } } }
