.faq {
	padding-top: 50px;
	padding-bottom: 133px;
	position: relative;
	@include r(1920) {
		padding-bottom: 107px;
		padding-top: 20px; }
	@include r(1024) {
		padding-bottom: 77px; }
	@include r(768) {
		padding-bottom: 60px; }
	@include r(479) {
		padding-bottom: 40px; }
	.fico {
		&-starB, &-starS {
			position: absolute; }
		&-starB {
			&--1 {
				left: calc(50% - 346px);
				top: 112px; }
			&--2 {
				left: calc(50% - -870px);
				top: 144px; } }
		&-starS {
			&--1 {
				left: calc(50% - 1070px);
				top: 519px; }
			&--2 {
				left: calc(50% - 890px);
				top: 317px;
				@include r(1300) {
					display: none; } }
			&--3 {
				left: calc(50% - -179px);
				top: 385px; }
			&--4 {
				right: calc(50% - 165px);
				top: 96px; } } }
	&__title {
		text-align: center;
		margin-bottom: 60px;
		margin-top: 127px;
		@include r(1920) {
			margin-top: 90px; }
		@include r(1300) {
			margin-top: 60px;
			margin-bottom: 50px; }
		@include r(768) {
			margin-bottom: 30px;
			margin-top: 50px; }
		@include r(479) {
			margin-bottom: 15px;
			margin-top: 24px; } }
	&__list {
		max-width: 1010px;
		width: 100%;
		margin: auto;
		margin-bottom: 68px;
		@include r(1920) {
			max-width: 770px; }
		@include r(1300) {
			max-width: 640px; }
		@include r(768) {
			margin-bottom: 50px; }
		@include r(640) {
			margin-bottom: 25px; } }
	&__bottom {
		text-align: center; }
	&__text {
		font-style: italic;
		color: $light-purple;
		margin-bottom: 20px;
		@include r(640) {
			margin-bottom: 11px; } }

	&__link {
		color: $dark-blue;
		text-decoration: none;
		font-size: 30px;
		border-bottom: 2px solid transparentize($dark-blue, .999);
		transition: all .3s;
		&:hover {
			border-bottom: 2px solid $dark-blue; }
		@include r(768) {
			font-size: 25px;
			line-height: 120%; }
		@include r(479) {
			font-size: 20px; } } }

.question {
	@include nlt {
		border-bottom: 1px solid transparentize($dark-blue, .9);
		padding-bottom: 14px;
		margin-bottom: 14px; }
	@include r(1920) {
		@include nlt {
			padding-bottom: 13px; } }
	@include r(768) {
		@include nlt {
			padding-bottom: 11px;
			margin-bottom: 11px; } }
	@include r(640) {
		@include nlt {
			padding-bottom: 9px;
			margin-bottom: 9px; } }
	&__top {
		display: flex;
		align-items: baseline;
		width: 100%;
		cursor: pointer;
		&.active {
			.fico {
				transform: rotate(180deg); }
			.question__bottom {
				display: block; } }
		&:hover {
			.question__title {
				text-decoration: underline; } } }
	&__left {
		display: flex;
		align-items: center;
		margin-right: 5px;
		@include r(640) {
			align-items: flex-start; } }
	&__ico {
		width: 32px;
		height: 32px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 2px solid $purple;
		flex-shrink: 0;
		margin-right: 28px;
		span {
			color: $purple;
			line-height: 1;
			font-size: 20px; }
		@include r(640) {
			width: 24px;
			height: 24px;
			margin-right: 16px;
			span {
				font-size: 15px; } } }
	.fico {
		margin-left: auto;
		font-size: 13px;
		color: $dark-blue;
		transition: all .3s;
		@include r(1024) {
			font-size: 11px; }
		@include r(640) {
			font-size: 9px; } }
	&__bottom {
		margin-left: 60px;
		display: none;
		margin-top: 10px;
		@include r(640) {
			margin-left: 40px; } }
	&__text {
		color: $light-purple;
		max-width: 87%; }
	&__list {
		margin-top: 5px; }
	.list {
		&__item {
			color: $light-purple;
			position: relative;
			padding-left: 25px;
			@include nlt {
				margin-bottom: 6px; }
			&::after {
				content: '';
				display: block;
				position: absolute;
				width: 6px;
				height: 6px;
				border-radius: 50%;
				top: 9px;
				left: 0;
				background-color: $purple; } } } }
