.menu {
	padding: 0 35px;
	@include r(1300) {
		padding: 0 30px; }
	@include r(640) {
		padding: 0 20px; }
	&__wrap {
		display: flex;
		justify-content: space-between;
		@include r(1024) {
			align-items: flex-end; }
		@include r(479) {
			padding-bottom: 12px;
			border-bottom: 1px solid $dark-blue; } }
	&__logo {
		@include r(640) {
			width: 120px; }
		@include r(479) {
			width: 97px; } }
	&__nav {
		display: flex;
		align-items: flex-end;
		margin-left: 61px;
		margin-bottom: 4px;
		@include r(1300) {
			margin-left: 0; }
		@include r(1024) {
			display: none; } }
	&__btn {
		margin-top: 10px;
		@include r(1024) {
			display: none; } } }


//mobile menu - burger
.burger {
	display: none;
	@include r(1024) {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		justify-content: center;
		cursor: pointer;
		width: 30px;
		height: 15px;
		margin-bottom: 4px; }
	@include r(479) {
		margin-bottom: 6px; }
	&__line {
		height: 1px;
		display: block;
		background-color: $dark-blue;
		position: relative;
		transition: all .4s;
		&:nth-child(1) {
			width: 30px;
			top: -6px; }
		&:nth-child(2) {
			width: 20px; }
		&:nth-child(3) {
			width: 25px;
			bottom: -6px; } }
	&--active {
		.burger__line {
			width: 30px;
			background-color: $dark-blue;
			&:nth-child(1) {
				transform: rotate(45deg);
				top: 1px; }
			&:nth-child(2) {
				display: none; }
			&:nth-child(3) {
				transform: rotate(-45deg);
				bottom: 0; } } } }
.overlay {
	opacity: 0;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	z-index: 40;
	background: transparentize(#53555A, .7);
	transition: all .5s;
	pointer-events: none;
	&.active {
		opacity: 1;
		pointer-events: visible; } }
