.recommendations {
	padding-bottom: 85px;
	@include r(1920) {
		padding-bottom: 91px; }
	@include r(1440) {
		padding-bottom: 90px; }
	@include r(1300) {
		padding-bottom: 69px; }
	@include r(1024) {
		padding-bottom: 60px; }
	@include r(479) {
		padding-bottom: 40px;
		padding-top: 23px; }
	&__title {
		text-align: center;
		max-width: 1118px;
		width: 100%;
		margin: auto;
		margin-top: 121px;
		margin-bottom: 77px;
		@include r(1920) {
			margin-top: 101px;
			margin-bottom: 49px; }
		@include r(1300) {
			margin-top: 61px;
			margin-bottom: 43px;
			max-width: 848px;
			line-height: 100%; }
		@include r(1024) {
			margin-bottom: 51px; }
		@include r(900) {
			margin-top: 50px;
			margin-bottom: 40px; }
		@include r(479) {
			font-size: 28px;
			margin-top: 4px;
			margin-bottom: 32px;
			line-height: 110%; } }
	&__list {
		display: flex;
		flex-wrap: wrap; } }
.planets {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	&::after {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		height: 1px;
		background: #E3E2EA;
		top: 50%;
		transform: translateY(-50%); }
	@include r(1300) {
		margin-left: 0; }
	@include r(640) {
		margin-left: 30px; }
	@include r(479) {
		position: relative;
		left: 10px;
		margin-left: 0;
		margin-bottom: 20px; }
	&__photo {
		padding: 0 10px;
		background-color: $white;
		z-index: 1;
		@include nlt {
			margin-right: 160px; }
		@include r(1920) {
			@include nlt {
				margin-right: 89px; } }
		@include r(1300) {
			@include nlt {
				margin-right: 70px; } }
		@include r(900) {
			padding: 0 7px;
			flex-shrink: 0;
			&:nth-of-type(1) {
				width: 30px; }
			&:nth-of-type(2) {
				width: 40px; }
			&:nth-of-type(3) {
				width: 51px; }
			&:nth-of-type(4) {
				width: 46px; }
			&:nth-of-type(5) {
				width: 95px; }
			&:nth-of-type(6) {
				width: 90px; }
			&:nth-of-type(7) {
				width: 50px; }
			&:nth-of-type(8) {
				width: 50px; }
			&:nth-of-type(9) {
				width: 27px; }
			@include nlt {
				margin-right: 40px; } }
		@include r(479) {
			&:nth-of-type(1) {
				width: 25px; }
			&:nth-of-type(2) {
				width: 35px; }
			&:nth-of-type(3) {
				width: 45px; }
			&:nth-of-type(4) {
				width: 41px; }
			&:nth-of-type(5) {
				width: 66px; }
			&:nth-of-type(6) {
				width: 63px; }
			&:nth-of-type(7) {
				width: 39px; }
			&:nth-of-type(8) {
				width: 45px; }
			&:nth-of-type(9) {
				width: 20px; }
			@include nlt {
				margin-right: 22px; } } } }
.recommendation {
	display: flex;
	align-items: center;
	min-height: 163px;
	&:not(:nth-child(3n+1)) {
		max-width: 520px;
		width: 100%;
		border-left: 1px solid transparentize($dark-blue, .9);
		padding-left: 30px; }
	&:nth-child(3n+1) {
		max-width: 490px;
		width: 100%; }
	&:nth-of-type(-n+3) {
		border-bottom: 1px solid transparentize($dark-blue, .9); }
	@include r(1920) {
		align-items: flex-start;
		min-height: auto;
		padding: 44px 0;
		&:nth-child(3n+1) {
			max-width: 370px; }
		&:not(:nth-child(3n+1)) {
			max-width: 400px; } }
	@include r(1300) {
		padding-bottom: 39px;
		&:nth-child(3n+1) {
			max-width: 305px; }
		&:not(:nth-child(3n+1)) {
			max-width: 327px;
			padding-left: 21px; } }
	@include r(1024) {
		padding: 43px 5px 43px 0;
		&:not(:nth-child(3n+1)), &:nth-child(3n+1) {
			width: calc(100%/3);
			max-width: 100%; } }
	@include r(900) {
		padding: 25px 5px 25px 0; }
	@include r(768) {
		&:not(:nth-child(3n+1)), &:nth-child(3n+1) {
			width: calc(100%/2);
			border-left: none; }
		&:nth-of-type(-n+4) {
			border-bottom: 1px solid transparentize($dark-blue, .9); }
		&:nth-of-type(even) {
			border-left: none;
			padding-left: 15px; }
		&:nth-of-type(odd) {
			border-right: 1px solid transparentize($dark-blue, .9);
			padding-left: 0; } }
	@include r(479) {
		align-items: center;
		padding-top: 0;
		&:not(:nth-child(3n+1)), &:nth-child(3n+1) {
			width: 100%;
			padding-left: 0;
			padding-bottom: 0;
			padding-right: 0; }
		&:nth-of-type(odd) {
			border-right: none; }
		@include nlt {
			padding-bottom: 14px;
			margin-bottom: 14px;
			border-bottom: 1px solid transparentize($dark-blue, .9); } }
	&__wrap-ico {
		width: 70px;
		margin-right: 29px;
		flex-shrink: 0;
		@include r(1300) {
			margin-right: 15px; }
		@include r(900) {
			width: 50px;
			margin-right: 10px; }
		@include r(640) {
			width: 45px; }
		@include r(479) {
			width: 37px;
			margin-right: 21px; } }
	&__text {
		color: $dark-blue;
		max-width: 73%;
		@include r(1920) {
			max-width: 64%;
			margin-top: 10px; }
		@include r(1300) {
			max-width: 70%; }
		@include r(1024) {
			max-width: 72%; }
		@include r(768) {
			margin-top: 0; }
		@include r(479) {
			max-width: 100%; } } }
