.header {
	padding-top: 30px;
	padding-bottom: 146px;
	background: url('../img/svg/rings.svg') no-repeat calc(50% - 454px) top, linear-gradient(180deg, #E2E5FF 0%, rgba(233, 235, 250, 0) 100%);
	position: relative;
	@include r(1920) {
		padding-bottom: 105px; }
	@include r(1300) {
		padding-bottom: 75px;
		background-position-x: calc(50% - 380px); }
	@include r(768) {
		padding-bottom: 60px;
		background-position-x: calc(50% - 260px);
		background-size: 650px; }
	@include r(640) {
		padding-top: 20px;
		padding-bottom: 50px; }
	@include r(479) {
		padding-bottom: 40px;
		padding-top: 12px;
		background-position-x: calc(50% - 91px);
		background-size: 420px; }
	.fico {
		&-starB, &-starS {
			position: absolute; }
		&-starB {
			&--1 {
				left: calc(50% - 873px);
				top: 136px; }
			&--2 {
				left: calc(50% - 390px);
				top: 474px; }
			&--3 {
				left: calc(50% - -980px);
				top: 310px; } }
		&-starS {
			&--1 {
				left: calc(50% - 1070px);
				top: 707px; }
			&--2 {
				left: calc(50% - 890px);
				top: 488px;
				@include r(1300) {
					display: none; } }
			&--3 {
				left: calc(50% - -218px);
				top: 260px; }
			&--4 {
				right: calc(50% - 784px);
				top: 575px; } } }
	&__title {
		text-align: center;
		max-width: 1270px;
		width: 100%;
		margin: auto;
		margin-top: 97px;
		margin-bottom: 126px;
		@include r(1920) {
			margin-bottom: 55px; }
		@include r(1300) {
			margin-top: 57px; }
		@include r(1024) {
			margin-top: 63px; }
		@include r(640) {
			margin-top: 50px;
			margin-bottom: 40px; }
		@include r(479) {
			max-width: 95%;
			margin-bottom: 44px; } }
	.container {
		transition: all 1s; }
	&__wrap {
		display: flex;
		justify-content: space-between;
		margin-bottom: 123px;

		@include r(1920) {
			margin-bottom: 76px; }
		@include r(1300) {
			margin-bottom: 64px; }
		@include r(768) {
			flex-wrap: wrap; }
		@include r(479) {
			margin-bottom: 27px; } }
	&__btn {
		margin: auto; } }

.advantage {
	text-align: center;
	padding: 17px 32px;
	@include nlt {
		border-right: 1px solid transparentize($dark-blue, .9); }
	&:nth-of-type(1), &:nth-of-type(4) {
		width: 370px; }
	&:nth-of-type(2), &:nth-of-type(3) {
		width: 390px; }
	&:nth-of-type(2) {
		.advantage__wrap-ico {
			position: relative;
			left: 10px; } }
	&:nth-of-type(4) {
		.advantage__wrap-ico {
			position: relative;
			left: 8px; } }
	@include r(1920) {
		padding: 17px 23px 0px; }
	@include r(1300) {
		padding: 17px 14px 0px; }
	@include r(1024) {
		padding: 17px 13px 0; }
	@include r(900) {
		padding: 10px 3px;
		&:nth-of-type(2), &:nth-of-type(4) {
			.advantage__wrap-ico {
				position: static; } } }
	@include r(768) {
		padding: 15px;
		&:nth-of-type(1), &:nth-of-type(2), &:nth-of-type(3), &:nth-of-type(4) {
			width: calc(100%/2); }
		&:nth-of-type(-n+2) {
			border-bottom: 1px solid transparentize($dark-blue, .9); }
		&:nth-of-type(even) {
			border-right: none; } }
	@include r(479) {
		display: flex;
		text-align: left;
		align-items: center;
		padding: 0;
		&:nth-of-type(1), &:nth-of-type(2), &:nth-of-type(3), &:nth-of-type(4) {
			width: 100%;
			border-right: none; }
		@include nlt {
			padding-bottom: 8px;
			margin-bottom: 11px;
			border-bottom: 1px solid transparentize($dark-blue, .9); }
		&:nth-of-type(1) {
			.advantage__ico {
				width: 30px; } }
		&:nth-of-type(2) {
			.advantage__ico {
				width: 26px; } }
		&:nth-of-type(3) {
			.advantage__ico {
				width: 35px; } }
		&:nth-of-type(4) {
			.advantage__ico {
				width: 29px; } } }
	&__wrap-ico {
		margin-bottom: 25px;
		@include r(1300) {
			margin-bottom: 28px; }
		@include r(900) {
			width: 60px;
			margin: auto;
			margin-bottom: 15px; }
		@include r(640) {
			height: 60px;
			display: flex;
			align-items: center;
			justify-content: center; }
		@include r(479) {
			margin: inherit;
			margin-right: 17px;
			flex-shrink: 0;
			margin-bottom: 0;
			width: 40px;
			height: auto;
			text-align: center; } } }
