.about {
	background: $dark-purple;
	padding-top: 110px;
	padding-bottom: 110px;
	position: relative;
	transition: all 1s;
	@include r(1300) {
		padding-top: 80px;
		padding-bottom: 41px; }
	@include r(1024) {
		padding-bottom: 33px; }
	@include r(768) {
		padding-top: 60px; }
	@include r(479) {
		padding: 40px 0; }

	.fico {
		&-starB, &-starS {
			position: absolute;
			color: transparentize($white, .8); }
		&-starB {
			&--1 {
				left: calc(50% - 1062px);
				top: 116px; }
			&--2 {
				left: calc(50% - 450px);
				top: 267px; }
			&--3 {
				left: calc(50% - -913px);
				top: 302px; } }
		&-starS {
			&--1 {
				left: calc(50% - 1080px);
				top: 496px; }
			&--2 {
				left: calc(50% - -686px);
				top: 584px;
				@include r(1300) {
					display: none; } }
			&--3 {
				left: calc(50% - -338px);
				top: 70px; } } }
	&__list {
		min-height: 255px;
		margin-bottom: 145px;
		@include r(1920) {
			min-height: 295px;
			margin-bottom: 106px; }
		@include r(1300) {
			min-height: 315px;
			margin-bottom: 36px; }
		@include r(768) {
			min-height: auto;
			margin-bottom: 30px; } }
	&__tabs {
		display: flex;
		justify-content: space-between; } }

.info {
	align-items: flex-start;
	display: none;
	opacity: 0;
	transition: all 1s;
	&.active {
		display: flex;
		opacity: 1; }
	@include r(768) {
		flex-wrap: wrap;
		align-items: center;
		&:nth-of-type(2) {
			.info__photo {
				height: 90px; } } }
	@include r(479) {
		&:nth-of-type(2) {
			.info__photo {
				height: auto;
				width: 60px; } } }
	&__title {
		max-width: 490px;
		width: 100%;
		margin-right: 30px;
		@include r(1920) {
			max-width: 370px; }
		@include r(1300) {
			max-width: 310px;
			margin-right: 20px; }
		@include r(1024) {
			max-width: 32.3%;
			flex-shrink: 0; }
		@include r(768) {
			max-width: 70%; }
		@include r(374) {
			max-width: 64%; } }
	&__desc {
		max-width: 744px;
		width: 100%;
		margin-top: 3px;
		@include r(768) {
			max-width: 100%;
			order: 1;
			margin-top: 18px; } }
	&__text {
		color: $white;
		max-width: 92%;
		margin-bottom: 49px;
		@include r(1920) {
			max-width: 97%;
			margin-bottom: 60px; }
		@include r(1300) {
			max-width: 94%; }
		@include r(900) {
			margin-bottom: 30px; }
		@include r(479) {
			font-size: 16px;
			line-height: 25px;
			max-width: 96%; }
		@include r(374) {
			max-width: 100%; } }
	&__photo {
		margin-left: auto;
		flex-shrink: 0;
		@include r(1024) {
			height: 150px; }
		@include r(900) {
			height: 130px; }
		@include r(768) {
			max-width: 18%;
			height: auto; }
		@include r(479) {
			width: 78px;
			max-width: 100%; } }
	&__btn {
		@include r(479) {
			width: 100%; } } }

.tab {
	width: 490px;
	display: flex;
	align-items: flex-end;
	color: transparentize($white, .7);
	cursor: pointer;
	padding-top: 9px;
	border-top: 1px solid transparentize($white, .8);
	transition: all .3s;
	position: relative;
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: -1px;
		left: 0;
		background-color: $white;
		height: 1px;
		width: 0;
		transition: width 7s, opacity .1s;
		opacity: 1;
		transition-timing-function: linear; }
	&:hover {
		color: $white; }
	&.active {
		color: $white;
		border-top: 1px solid $white; }
	&.animate {
		&::after {
			width: 100%;
			opacity: 1; }
		color: $white; }
	&.hide {
		&::after {
			opacity: 0; } }
	@include r(1920) {
		width: 370px; }
	@include r(1300) {
		width: 310px; }
	@include r(1024) {
		width: calc(100%/3 - 10px);
		align-items: center;
		padding-top: 16px; }
	@include r(768) {
		align-items: flex-start; }
	@include r(640) {
		padding-top: 14px; }
	.fico {
		margin-right: 20px;
		&-about1 {
			font-size: 55px;
			margin-left: 1px; }
		&-about2 {
			font-size: 69px;
			margin-left: 9px; }
		&-about3 {
			font-size: 64px;
			margin-left: 4px; }
		@include r(900) {
			margin-right: 15px;
			&-about1 {
				font-size: 50px;
				margin-left: 0px; }
			&-about2 {
				font-size: 64px;
				margin-left: 0px; }
			&-about3 {
				font-size: 59px;
				margin-left: 0px; } }
		@include r(768) {
			display: none; } }
	&__text {
		max-width: 50%;
		margin-bottom: 3px;
		@include r(1920) {
			max-width: 61%; }
		@include r(1024) {
			margin-bottom: 0; }
		@include r(768) {
			max-width: 90%; }
		@include r(479) {
			font-size: 13px;
			line-height: 17px; }
		@include r(374) {
			font-size: 12px;
			max-width: 100%; } } }
