.footer {
	padding-top: 100px;
	padding-bottom: 100px;
	@include r(1920) {
		padding: 90px 0; }
	@include r(1300) {
		padding: 70px 0; }
	@include r(768) {
		padding: 60px 0; }
	@include r(640) {
		padding: 30px 0; }
	&__wrap {
		display: flex;
		align-items: flex-start;
		@include r(640) {
			flex-direction: column;
			text-align: center;
			flex-wrap: wrap; } }
	&__column {
		max-width: 360px;
		width: 100%;
		@include r(1920) {
			max-width: 270px; }
		@include r(1300) {
			max-width: 225px; }
		@include r(1024) {
			max-width: 23.4%; }
		@include r(640) {
			max-width: 100%; } }
	&__left {
		margin-right: 30px;
		@include r(1300) {
			margin-right: 20px; }
		@include r(640) {
			margin-right: 0;
			order: 1; } }
	&__links {
		flex: 1;
		margin-top: 13px;
		@include r(1920) {
			margin-top: 11px; }
		@include r(1024) {
			margin-right: 20px; }
		@include r(640) {
			margin-right: 0;
			margin-top: 0;
			order: -1;
			padding-bottom: 35px;
			margin-bottom: 20px;
			border-bottom: 1px solid transparentize($dark-blue, .8); } }
	&__logo {
		width: 97px;
		margin-bottom: 22px;
		@include r(640) {
			display: none; } }
	&__copyright {
		margin-bottom: 50px;
		@include r(768) {
			margin-bottom: 30px; }
		@include r(640) {
			margin-bottom: 20px; } }
	&__contacts {
		margin-top: 12px;
		@include r(1920) {
			margin-top: 9px; }
		@include r(1024) {
			margin-top: 12px; }
		@include r(640) {
			padding-bottom: 30px;
			margin-bottom: 31px;
			border-bottom: 1px solid transparentize($dark-blue, .8); } }
	&__social {
		@include r(640) {
			justify-content: center; } } }

.copyright {
	color: $light-purple;
	font-size: 16px;
	@include r(900) {
		font-size: 15px; }
	@include r(640) {
		font-size: 16px;
		line-height: 23px; } }

.links {
	display: flex;
	align-items: flex-start;
	@include r(640) {
		flex-direction: column;
		align-items: center;
		width: 100%; }
	&__col {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		@include nlt {
			margin-right: 205px; }
		@include r(1920) {
			@include nlt {
				margin-right: 116px; } }
		@include r(1680) {
			@include nlt {
				margin-right: 107px; } }
		@include r(1300) {
			@include nlt {
				margin-right: 70px; } }
		@include r(900) {
			@include nlt {
				margin-right: 40px; } }
		@include r(768) {
			@include nlt {
				margin-right: 20px; } }
		@include r(640) {
			align-items: center;
			@include nlt {
				margin-right: 0;
				margin-bottom: 12px; } } }
	&__item {
		text-decoration: none;
		color: $dark-blue;
		transition: all .3s;
		@include nlt {
			margin-bottom: 27px; }
		&:hover {
			text-decoration: underline; }
		@include r(1920) {
			@include nlt {
				margin-bottom: 26px; } }
		@include r(1300) {
			@include nlt {
				margin-bottom: 22px; } }
		@include r(768) {
			@include nlt {
				margin-bottom: 17px; } }
		@include r(640) {
			text-align: center;
			font-size: 16px;
			line-height: 20px;
			@include nlt {
				margin-bottom: 12px; } }
		@include r(479) {
			font-size: 16px; } } }
.contacts {
	&__email {
		color: $dark-blue;
		text-decoration: none;
		font-size: 20px;
		display: inline-block;
		margin-bottom: 14px;
		transition: all .3s;
		border-bottom: 1px solid transparentize($dark-blue, .999);
		&:hover {
			border-bottom: 1px solid $dark-blue; }
		@include r(768) {
			font-size: 16px;
			margin-bottom: 10px; }
		@include r(640) {
			font-size: 20px;
			margin-bottom: 22px; } }
	&__address {
		color: $light-purple;
		@include r(900) {
			font-size: 15px;
			line-height: 20px; }
		@include r(640) {
			font-size: 16px;
			line-height: 23px; } } }
.social {
	display: flex;
	align-items: flex-end;
	&__link {
		text-decoration: none;
		@include nlt {
			margin-right: 25px; }
		&:hover {
			.fico {
				color: #B0A8CE; } } }
	.fico {
		color: $dark-blue;
		transition: all .3s;
		&-fb {
			font-size: 25px; }
		&-tw {
			font-size: 20px; }
		&-inst {
			font-size: 21px; } } }
