.banner {
	padding-top: 118px;
	padding-bottom: 107px;
	background-color: $dark-purple;
	position: relative;
	@include r(1920) {
		padding: 100px 0; }
	@include r(1024) {
		padding: 88px 0; }
	@include r(768) {
		padding: 60px 0; }
	@include r(479) {
		padding: 40px 0; }
	.fico {
		&-starB, &-starS {
			position: absolute;
			color: transparentize($white, .8); }
		&-starB {
			&--1 {
				left: calc(50% - 955px);
				top: 42px; }
			&--2 {
				left: calc(50% - 459px);
				bottom: 20px; }
			&--3 {
				left: calc(50% - -953px);
				top: 195px; } }
		&-starS {
			&--1 {
				left: calc(50% - 970px);
				bottom: 20px; }
			&--2 {
				left: calc(50% - -170px);
				top: 151px; } } }
	&__title {
		text-align: center;
		max-width: 950px;
		width: 100%;
		margin: auto;
		margin-bottom: 52px;
		@include r(1680) {
			max-width: 830px; }
		@include r(900) {
			max-width: 80%;
			margin-bottom: 30px; }
		@include r(640) {
			max-width: 100%;
			margin-bottom: 23px; } }
	&__btn {
		margin: auto;
		width: 388px;
		@include r(1680) {
			width: 340px; } } }
