body {
	font-family: 'IBM Plex Sans', sans-serif;
	font-size: 18px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscal;
	text-size-adjust: none;
	-moz-text-size-adjust: none;
	-webkit-text-size-adjust: none;
	-ms-text-size-adjust: none;
	overflow-x: hidden; }

html {
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	overflow-x: hidden; }
::-ms-clear {
	display: none; }
select::-ms-expand {
	display: none; }
select:-moz-focusring, a:-moz-focusring {
	color: transparent;
	text-shadow: 0 0 0 #000; }

*:focus, *:visited, *:active, *:hover {
	outline: 0!important; }
a, a:focus, a:visited, a:active, a:hover {
	outline: 0!important; }
*::-moz-focus-inner {
	border: 0;
	outline: none; }
img {
	max-width: 100%; }

// Font icons
.fico {
	display: inline-block;
	font-size: 3rem;
	&::before {
		display: block; } }


//title
.title {
	font-family: 'Tenor Sans', sans-serif;
	&--dark {
		color: $dark-blue; }
	&--light {
		color: $white; }
	&--main {
		font-size: 80px;
		line-height: 100%;
		@include r(1920) {
			font-size: 70px; }
		@include r(1300) {
			font-size: 60px; }
		@include r(900) {
			font-size: 53px; }
		@include r(768) {
			font-size: 50px; }
		@include r(640) {
			font-size: 34px; }
		@include r(479) {
			font-size: 30px; }
		@include r(374) {
			font-size: 26px; } }
	&--big {
		font-size: 50px;
		line-height: 50px;
		@include r(1300) {
			font-size: 40px;
			line-height: 110%; }
		@include r(640) {
			font-size: 36px; }
		@include r(479) {
			font-size: 32px; }
		@include r(374) {
			font-size: 30px; } }
	&--medium {
		font-size: 45px;
		line-height: 50px;
		@include r(1300) {
			font-size: 40px;
			line-height: 44px; }
		@include r(900) {
			font-size: 33px;
			line-height: 110%; }
		@include r(640) {
			font-size: 30px; }
		@include r(479) {
			font-size: 25px; } }
	&--small {
		font-size: 40px;
		line-height: 40px;
		@include r(768) {
			font-size: 35px;
			line-height: 110%; }
		@include r(479) {
			font-size: 30px; } }
	&--least {
		font-size: 30px;
		line-height: 36px;
		@include r(768) {
			font-size: 25px;
			line-height: 120%; }
		@include r(640) {
			font-size: 23px; }
		@include r(479) {
			font-size: 20px; } } }
//btn
.btn {
	cursor: pointer;
	outline: none;
	border: none;
	max-width: 100%;
	text-align: left;
	display: flex;
	align-items: center;
	text-decoration: none;
	justify-content: space-between;
	.fico {
		font-size: 15px; }
	&--empty {
		border-radius: 30px;
		color: $dark-blue;
		font-size: 15px;
		line-height: 28px;
		padding: 5px 18px;
		border: 1px solid $purple;
		width: 210px;
		background: none;
		letter-spacing: -0.2px;
		transition: all .3s;
		.fico {
			color: $purple; }
		&:hover {
			background: #DCE0FF; } }
	&--full {
		font-family: 'Tenor Sans', sans-serif;
		background: $purple;
		border-radius: 50px;
		color: $white;
		font-size: 20px;
		line-height: 30px;
		padding: 10px 20px;
		width: 370px;
		transition: all .3s;
		.fico {
			color: $white; }
		&:hover {
			box-shadow: 0px 10px 15px rgba(82, 69, 138, 0.3); }
		@include r(1300) {
			width: 310px; }
		@include r(479) {
			width: 100%;
			font-size: 18px; } } }

//text
.fzXS {
	font-size: 16px;
	&--heightBig {
		line-height: 25px; }
	&--heightSmall {
		line-height: 20px; } }
.fzS {
	font-size: 17px;
	&--heightBig {
		line-height: 30px; }
	&--heightSmall {
		line-height: 25px; } }
.fzM {
	font-size: 18px;
	&--heightBig {
		line-height: 30px; }
	&--heightMed {
		line-height: 25px; }
	&--heightSmall {
		line-height: 20px; }
	@include r(1300) {
		font-size: 16px;
		&--heightBig {
			line-height: 25px; }
		&--heightMed {
			line-height: 25px; } }
	@include r(900) {
		&--heightMed {
			line-height: 20px; } }
	@include r(479) {
		font-size: 15px;
		line-height: 20px;
		&--heightBig {
			line-height: 25px; } } }
.fzL {
	font-size: 20px;
	line-height: 25px; }
.fzXL {
	font-size: 22px;
	line-height: 25px;
	@include r(1300) {
		font-size: 20px; }
	@include r(768) {
		font-size: 18px; }
	@include r(479) {
		font-size: 17px;
		line-height: 20px; } }
.fzBig {
	font-size: 25px;
	line-height: 30px;
	@include r(1300) {
		font-size: 23px; }
	@include r(1024) {
		font-size: 22px; }
	@include r(900) {
		font-size: 20px; } }


//menu
.nav {
	font-family: 'Tenor Sans', sans-serif;
	&__item {
		color: $dark-blue;
		text-decoration: none;
		border-bottom: 1px solid transparentize($dark-blue, .999);
		transition: all .3s;
		&:hover {
			border-bottom: 1px solid $dark-blue; }
		@include nlt {
			margin-right: 86px; }
		@include r(1680) {
			@include nlt {
				margin-right: 63px; } }
		@include r(1440) {
			@include nlt {
				margin-right: 48px; } }
		@include r(1300) {
			font-size: 18px;
			@include nlt {
				margin-right: 25px; } } } }
//stars
.fico {
	&-starB, &-starS {
		color: transparentize($light-purple, .6); }
	&-starB {
		font-size: 33px;
		@include r(1920) {
			font-size: 23px; } }
	&-starS {
		font-size: 17px;
		@include r(1920) {
			font-size: 13px; } } }


//sliders
.slick-slide {
	outline: none; }
.slick-dots {
	display: flex;
	justify-content: center;
	align-items: center;
	&--dark {
		li {
			border: 1px solid transparentize($purple, .6);
			&.slick-active {
				border: 1px solid $purple; } } }
	&--light {
		li {
			border: 1px solid transparentize($white, .6);
			&.slick-active {
				border: 1px solid $white; } } }
	li {
		width: 8px;
		height: 8px;
		border-radius: 50%;
		margin: 0 16px;
		flex-shrink: 0;
		cursor: pointer;
		@include r(479) {
			margin: 0 11px; } }
	button {
		display: none; } }

.sun {
	text-align: center;
	border-bottom: 1px solid transparentize(#7E7B88, .8);
	font-size: 0;
	&__img {
		@include r(768) {
			width: 240px; }
		@include r(479) {
			width: 160px; } } }


.tree {
	text-align: center;
	position: relative;
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 43%;
		left: 0;
		right: 0;
		transform: translateY(-50%);
		height: 1px;
		background: transparentize($purple, .8);
		z-index: -1; }
	&__img {
		background-color: $white;
		padding: 0 23px;
		@include r(640) {
			width: 150px; }
		@include r(479) {
			width: 83px;
			padding: 0 14px; } } }


.hideBlock {
	@include r(1024) {
		transform: translateX(-100%); } }
