.price {
	background: linear-gradient(180deg, #EFF3FE 0%, rgba(255, 255, 255, 0) 100%);
	padding-top: 129px;
	padding-bottom: 47px;
	position: relative;
	@include r(1920) {
		padding-top: 99px; }
	@include r(1300) {
		padding-top: 69px;
		padding-bottom: 33px; }
	@include r(768) {
		padding-top: 60px; }
	@include r(479) {
		padding-top: 40px; }
	.fico {
		&-starB, &-starS {
			position: absolute; }
		&-starB {
			&--1 {
				left: calc(50% - 1177px);
				top: 352px; }
			&--2 {
				left: calc(50% - 902px);
				bottom: 47px; }
			&--3 {
				right: calc(50% - 871px);
				top: 165px; } }
		&-starS {
			&--1 {
				left: calc(50% - 928px);
				top: 639px; }
			&--2 {
				right: calc(50% - 891px);
				top: 550px; }
			&--3 {
				right: calc(50% - 1088px);
				top: 784px; } } }
	.slick-slide {
		@include r(900) {
			margin: 0 10px;
			width: 370px;
			padding-bottom: 40px; }
		@include r(479) {
			width: 260px; } }
	.slick-list {
		@include r(900) {
			margin: 0 -30px; } }
	.slick-dots {
		margin-top: -20px; }
	&__title {
		text-align: center;
		max-width: 645px;
		width: 100%;
		margin: auto;
		margin-bottom: 81px;
		@include r(1300) {
			margin-bottom: 39px;
			font-size: 50px;
			line-height: 100%; }
		@include r(900) {
			margin-bottom: 40px; }
		@include r(768) {
			font-size: 45px; } }
	&__steps {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		counter-reset: section;
		margin-bottom: 115px;
		@include r(1920) {
			margin-bottom: 79px; }
		@include r(1300) {
			margin-bottom: 50px; }
		@include r(900) {
			display: none; } }
	&__cards {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 72px;
		@include r(1300) {
			margin-bottom: 42px; }
		@include r(900) {
			display: block; }
		@include r(479) {
			margin-bottom: 30px; } }
	&__guarantees {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@include r(900) {
			flex-wrap: wrap; } }
	&__left {
		display: flex;
		align-items: flex-start;
		@include r(900) {
			width: 100%;
			margin-bottom: 15px;
			align-items: center; }
		@include r(479) {
			margin-bottom: 10px; } }
	&__img {
		margin-right: 30px;
		@include r(1300) {
			margin-right: 20px;
			width: 62px; }
		@include r(479) {
			margin-right: 13px;
			width: 44px; } }
	&__text {
		font-size: 35px;
		line-height: 35px;
		@include r(1300) {
			font-size: 30px;
			line-height: 32px; }
		@include r(1024) {
			font-size: 27px;
			line-height: 28px; }
		@include r(768) {
			font-size: 25px;
			line-height: 26px; }
		@include r(479) {
			font-size: 21px;
			line-height: 22px; } }
	&__desc {
		color: $light-purple;
		margin-top: 7px;
		@include r(900) {
			width: calc(50% - 20px); }
		@include r(768) {
			width: 100%;
			margin-bottom: 20px; }
		@include r(479) {
			line-height: 25px;
			font-size: 16px; } }
	&__nasa {
		margin-top: 8px;
		@include r(900) {
			width: 50%; }
		@include r(768) {
			width: 100%; } } }
.step {
	width: calc(100%/5 - 24px);
	position: relative;
	padding-top: 50px;
	&::before {
		counter-increment: section;
		content: '0' counter(section);
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		font-family: 'Tenor Sans', sans-serif;
		color: $purple;
		font-size: 30px; }
	@include r(1300) {
		width: calc(100%/5 - 16px); }
	@include r(768) {
		display: none; }
	.fico {
		color: $purple;
		font-size: 12px;
		@include r(1920) {
			font-size: 9px; }
		@include r(1300) {
			font-size: 7.5px; }
		@include r(1024) {
			font-size: 36%; }
		@include r(900) {
			font-size: 30%; } }
	&__text {
		color: $light-purple;
		margin-top: 19px;
		@include r(1300) {
			font-size: 16px; }
		@include r(1024) {
			font-size: 14px;
			line-height: 21px; }
		@include r(900) {
			margin-top: 10px; } } }

.card {
	border-radius: 10px;
	padding: 39px 49px 46px;
	display: flex !important;
	flex-direction: column;
	transition: all .3s;
	border: 1px solid transparentize($white, .999);
	&--blue {
		background-color: $blue;
		min-height: 490px;
		&:hover {
			border: 1px solid #B8C6EC;
			box-shadow: 0px 10px 30px rgba(69, 96, 166, 0.2); }
		.card__btn {
			background: #4560A6;
			&:hover {
				background: #5376D0;
				box-shadow: 0px 10px 15px rgba(69, 96, 166, 0.3); } }
		.cost {
			color: #4560A6; } }
	&--purple {
		background-color: $light-purple2;
		min-height: 540px;
		&:hover {
			border: 1px solid #B9B9F2;
			box-shadow: 0px 15px 30px rgba(78, 77, 158, 0.2); }
		.card__btn {
			background: #4E4D9E;
			&:hover {
				background: #5F5EC8;
				box-shadow: 0px 10px 15px rgba(78, 77, 158, 0.3); } }
		.cost {
			color: #4E4D9E; } }
	&--pink {
		background-color: $light-pink;
		min-height: 490px;
		&:hover {
			border: 1px solid #CABFF4;
			box-shadow: 0px 25px 30px rgba(82, 69, 138, 0.2); }
		.card__btn {
			background: $purple;
			&:hover {
				background: #715FBE;
				box-shadow: 0px 10px 15px rgba(82, 69, 138, 0.3); } }
		.cost {
			color: $purple; } }
	@include r(1920) {
		padding: 39px 31px 35px; }
	@include r(1300) {
		padding: 29px 26px 20px;
		&--purple {
			padding-top: 36px;
			min-height: 480px;
			.card__top {
				margin-bottom: 31px; } }
		&--blue, &--pink {
			min-height: 440px; } }
	@include r(900) {
		&--blue, &--pink {
			margin-top: 20px; } }
	@include r(479) {
		padding: 29px 15px 20px;
		&--purple {
			padding-top: 29px;
			min-height: 460px;
			.card__top {
				margin-bottom: 25px; } }
		&--blue, &--pink {
			min-height: 440px;
			margin-top: 10px; } }
	&__top {
		text-align: center;
		margin-bottom: 32px;
		@include r(1300) {
			margin-bottom: 23px; } }
	&__type {
		font-size: 32px;
		margin-bottom: 15px;
		@include r(1024) {
			font-size: 28px; }
		@include r(479) {
			margin-bottom: 12px; } }
	&__list {
		margin-bottom: 10px; }
	.list {
		&__item {
			position: relative;
			display: flex;
			align-items: center;
			color: $dark-blue;
			@include nlt {
				padding-bottom: 10px;
				margin-bottom: 11px;
				border-bottom: 1px solid transparentize($dark-blue, .9); }
			@include r(479) {
				font-size: 14px;
				@include nlt {
					padding-bottom: 11px;
					margin-bottom: 8px; } } }
		.fico {
			color: $dark-blue;
			font-size: 16px;
			margin-right: 25px;
			@include r(479) {
				margin-right: 10px; } }
		.note {
			font-style: italic;
			color: $light-purple;
			font-size: 14px; } }
	&__btn {
		width: 100%;
		margin-top: auto;
		transition: all .3s;
		font-size: 18px;
		@include r(1300) {
			span {
				display: none; } } } }
.cost {
	&__old {
		font-size: 17px;
		margin-bottom: 4px;
		text-decoration: line-through;
		@include r(479) {
			font-size: 15px; } }
	&__new {
		font-size: 30px;
		@include r(479) {
			font-size: 25px; } } }


.column {
	max-width: 490px;
	width: 100%;
	@include r(1920) {
		max-width: 370px; }
	@include r(1300) {
		max-width: 310px; }
	@include r(1024) {
		max-width: 100%;
		width: calc(100%/3 - 10px); } }

.nasa {
	display: flex;
	align-items: flex-start;
	@include r(479) {
		display: block; }
	&__img {
		margin-right: 28px;
		@include r(1300) {
			margin-right: 28px;
			width: 60px; }
		@include r(479) {
			margin-right: 0;
			margin-bottom: 6px;
			width: 46px; } }
	&__text {
		font-style: italic;
		margin-bottom: 14px;
		color: $light-purple;
		@include r(1300) {
			margin-bottom: 11px;
			font-size: 14px;
			line-height: 20px; }
		@include r(479) {
			font-size: 14px;
			line-height: 24px; } }
	&__link {
		color: $dark-blue;
		text-decoration: none;
		display: flex;
		align-items: center;
		cursor: pointer;
		&:hover {
			span {
				text-decoration: underline; } }
		@include r(1300) {
			font-size: 15px; }
		@include r(479) {
			font-size: 16px; }
		.fico {
			color: $dark-blue;
			font-size: 15px;
			margin-left: 19px; } } }
