.reviews {
	padding-top: 100px;
	padding-bottom: 32px;
	position: relative;
	@include r(1920) {
		padding-top: 40px; }
	@include r(1300) {
		padding-bottom: 0; }
	.fico {
		&-map {
			position: absolute;
			left: 194px;
			color: transparentize($purple, .8);
			font-size: 412px;
			top: 609px;
			@include r(1920) {
				top: 569px;
				left: 64px; }
			@include r(1300) {
				top: 489px;
				left: -246px; } }
		&-starB, &-starS {
			position: absolute; }
		&-starB {
			&--1 {
				left: calc(50% - 277px);
				top: 79px; }
			&--2 {
				left: calc(50% - 887px);
				bottom: 55px; }
			&--3 {
				right: calc(50% - 1161px);
				top: 110px; } }
		&-starS {
			&--1 {
				left: calc(50% - 1121px);
				top: 548px; }
			&--2 {
				left: calc(50% - 921px);
				top: 313px; }
			&--3 {
				right: calc(50% - 343px);
				top: 394px; }
			&--4 {
				right: calc(50% - 310px);
				top: 60px; } } }
	&__title {
		text-align: center;
		max-width: 970px;
		width: 100%;
		margin: auto;
		margin-top: 84px;
		margin-bottom: 92px;
		@include r(1920) {
			margin-top: 62px; }
		@include r(1300) {
			margin-top: 42px;
			margin-bottom: 62px; }
		@include r(768) {
			margin-top: 40px;
			margin-bottom: 50px; }
		@include r(640) {
			margin-top: 30px;
			margin-bottom: 40px; }
		@include r(479) {
			margin-top: 13px;
			margin-bottom: 30px; } }
	&__numbers {
		display: flex;
		justify-content: space-between;
		margin-bottom: 99px;
		@include r(1920) {
			margin-bottom: 76px; }
		@include r(1300) {
			margin-bottom: 66px; }
		@include r(768) {
			margin-bottom: 50px; }
		@include r(640) {
			margin-bottom: 25px; } }

	.slick-list {
		margin: 0 -15px;
		padding-bottom: 30px;
		@include r(1300) {
			margin: 0 -10px; }
		@include r(1024) {
			margin: 0;
			margin-right: -30px; } }
	.slick-slide {
		width: 750px;
		margin: 15px;
		@include r(1300) {
			margin: 10px; }
		@include r(1024) {
			width: 600px; }
		@include r(640) {
			width: 280px; }
		@include r(374) {
			width: 265px; } } }
.number {
	display: flex;
	align-items: center;
	width: calc(100%/3 - 20px);
	@include r(1300) {
		width: calc(100%/3 - 10px); }
	@include r(640) {
		flex-direction: column;
		align-items: flex-start; }
	&__count {
		margin-right: 15px;
		font-size: 80px;
		color: $dark-blue;
		white-space: nowrap;
		@include r(1300) {
			font-size: 65px;
			margin-right: 12px; }
		@include r(1024) {
			font-size: 55px; }
		@include r(768) {
			font-size: 40px; }
		@include r(640) {
			margin-right: 0; }
		@include r(479) {
			font-size: 34px;
			margin-bottom: 9px; }
		@include r(374) {
			font-size: 30px; } }
	&__text {
		font-style: italic;
		color: $dark-blue;
		max-width: 28%;
		font-size: 16px;
		line-height: 20px;
		@include r(1920) {
			max-width: 36%; }
		@include r(1300) {
			max-width: 42%;
			font-size: 14px;
			line-height: 18px; }
		@include r(768) {
			max-width: 50%; }
		@include r(640) {
			max-width: 100%; }
		@include r(479) {
			font-size: 13px;
			line-height: 16px; } } }

.review {
	display: flex !important;
	align-items: flex-start;
	padding: 50px 50px 75px;
	border-radius: 10px;
	position: relative;
	&::before {
		content: '';
		display: block;
		position: absolute;
		left: 31px;
		bottom: 1px;
		transform: translateY(100%);
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 30px 30px 0 0; }
	&--blue {
		background: $blue;
		&::before {
			border-color: $blue transparent transparent transparent; } }
	&--pink {
		background: $pink;
		&::before {
			border-color: $pink transparent transparent transparent; } }
	&--green {
		background: $green;
		&::before {
			border-color: $green transparent transparent transparent; } }
	&--beige {
		background: $beige;
		&::before {
			border-color: $beige transparent transparent transparent; } }
	@include r(1920) {
		padding: 30px 30px 35px; }
	@include r(1300) {
		padding: 19px 18px 30px; }
	@include r(640) {
		flex-direction: column; }
	&__left {
		flex-shrink: 0;
		width: 120px;
		margin-right: 50px;
		@include r(1300) {
			margin-right: 20px; }
		@include r(640) {
			margin-right: 0;
			margin-bottom: 16px;
			width: 100%; } }
	&__wrap-photo {
		width: 100%;
		height: 120px;
		overflow: hidden;
		border-radius: 50%;
		margin-bottom: 24px;
		@include r(1300) {
			margin-bottom: 28px; }
		@include r(640) {
			width: 120px; } }
	&__name {
		font-size: 20px;
		color: $dark-blue;
		line-height: 20px;
		margin-bottom: 9px; }
	&__age {
		color: #777777;
		font-size: 14px; }
	&__right {
		position: relative;
		.fico {
			color: $dark-blue;
			font-size: 34px;
			margin-bottom: 28px; }
		@include r(1300) {
			margin-top: 5px; }
		@include r(1024) {
			.fico {
				font-size: 30px;
				margin-bottom: 20px; } }
		@include r(640) {
			margin-top: 0;
			.fico {
				margin-bottom: 0;
				position: absolute;
				top: -210px;
				right: 0; } }
		@include r(479) {
			.fico {
				font-size: 22px;
				right: 5px;
				top: -202px; } } }
	&__text {
		color: $dark-blue;
		@include r(479) {
			font-size: 16px; } } }
