.celebrities {
	padding-top: 103px;
	padding-bottom: 77px;
	background-color: $dark-purple;
	position: relative;
	overflow: hidden;
	@include r(1300) {
		padding-top: 75px;
		padding-bottom: 44px; }
	@include r(768) {
		padding: 60px 0; }
	@include r(479) {
		padding: 40px 0; }
	.fico-map {
		position: absolute;
		right: -51px;
		color: rgba(255,255,255,.1);
		font-size: 342px;
		top: 129px; }
	&__title {
		text-align: center;
		max-width: 530px;
		width: 100%;
		margin: auto;
		margin-bottom: 50px;
		@include r(1300) {
			font-size: 50px;
			line-height: 100%; }
		@include r(768) {
			font-size: 45px; }
		@include r(640) {
			max-width: 510px;
			margin-bottom: 24px; } }
	.slick-list {
		margin: 0 -15px;
		@include r(1300) {
			margin: 0 -10px; }
		@include r(1024) {
			margin: 0;
			margin-right: -30px; } }
	.slick-dots {
		margin-top: 89px;
		@include r(1920) {
			margin-top: 41px; } }
	.slick-slide {
		width: 360px;
		margin: 15px;
		@include r(1300) {
			margin: 10px; }
		@include r(640) {
			width: 280px; }
		@include r(374) {
			width: 265px; } }
	.slick-arrow {
		position: absolute;
		top: -102px;
		border: none;
		cursor: pointer;
		outline: 0;
		z-index: 1;
		background: none;
		font-size: 0;
		overflow: visible;
		padding: 0;
		.fico {
			font-size: 40px;
			outline: 0;
			color: transparentize($white, .6);
			transition: all .3s;
			line-height: 1; }
		&:hover {
			.fico {
				color: $white; } }
		&.next {
			right: 0;
			padding-right: 0; }
		&.prev {
			left: 0;
			transform: scaleX(-1.1); }
		@include r(768) {
			top: -90px;
			.fico {
				font-size: -100px; } }
		@include r(640) {
			display: none !important; } } }
.celebrity {
	display: flex !important;
	flex-direction: column;
	&__wrap-photo {
		height: 200px;
		width: 200px;
		overflow: hidden;
		border-radius: 50%;
		margin-bottom: 31px;
		@include r(1300) {
			margin-bottom: 27px; }
		@include r(1024) {
			width: 180px;
			height: 180px; }
		@include r(640) {
			width: 200px;
			height: 200px;
			margin-bottom: 20px; } }
	&__info {
		margin-bottom: 40px;
		min-height: 266px;
		@include r(1920) {
			min-height: 315px; }
		@include r(1300) {
			min-height: 321px; }
		@include r(1024) {
			min-height: 350px;
			margin-bottom: 20px; }
		@include r(900) {
			min-height: 318px; }
		@include r(768) {
			min-height: 249px; }
		@include r(640) {
			min-height: 248px; }
		@include r(374) {
			min-height: 278px; } }
	&__name {
		color: $white;
		margin-bottom: 15px;
		font-size: 25px;
		line-height: 30px;
		@include r(1300) {
			font-size: 18px; }
		@include r(768) {
			font-size: 25px; } }
	&__text {
		color: transparentize($white, .4); }
	&__bottom {
		display: flex;
		align-items: center;
		padding-top: 20px;
		margin-top: auto;
		width: 100%;
		border-top: 1px solid transparentize($white, .8);
		@include r(1024) {
			flex-direction: column; }
		@include r(768) {
			flex-direction: row; } }
	&__date {
		color: #AEABBC;
		font-style: italic;
		margin-left: 10px;
		@include r(1300) {
			font-size: 14px; }
		@include r(1024) {
			margin-left: 0; }
		@include r(768) {
			margin-left: 10px; } } }
.zodiac {
	display: flex;
	align-items: center;
	@include r(1024) {
		margin-bottom: 5px; }
	@include r(768) {
		margin-bottom: 0; }
	.fico {
		margin-right: 15px;
		color: $white;
		font-size: 23px;
		@include r(1300) {
			margin-right: 9px;
			font-size: 20px; }
		@include r(768) {
			font-size: 23px;
			margin-right: 15px; } }
	&__name {
		color: $white;
		@include r(1300) {
			font-size: 14px; } } }

