.map {
	padding-top: 120px;
	padding-bottom: 128px;
	position: relative;
	@include r(1920) {
		padding-top: 100px;
		padding-bottom: 100px; }
	@include r(1300) {
		padding-top: 70px;
		padding-bottom: 70px; }
	@include r(768) {
		padding: 60px 0; }
	@include r(479) {
		padding: 40px 0; }
	.fico {
		&-mapIco {
			position: absolute;
			left: calc(50% - 648px);
			top: calc(50% - 83px);
			font-size: 135px;
			color: transparentize($dark-blue, .8);
			@include r(1920) {
				left: calc(50% - 518px);
				top: calc(50% - 33px); }
			@include r(1300) {
				left: calc(50% - 408px);
				top: calc(50% - 63px); }
			@include r(900) {
				display: none; } }
		&-starB, &-starS {
			position: absolute; }
		&-starB {
			font-size: 23px;
			&--1 {
				left: calc(50% - 234px);
				top: 3%; }
			&--2 {
				right: calc(50% - 712px);
				top: 5%; } }
		&-starS {
			font-size: 12px;
			&--1 {
				left: calc(50% - 662px);
				top: 13%; }
			&--2 {
				left: calc(50% - 792px);
				top: 23%; }
			&--3 {
				right: calc(50% - 172px);
				top: 16%; }
			&--4 {
				right: calc(50% - 152px);
				top: 3%; } } }
	&__title {
		text-align: center;
		max-width: 970px;
		width: 100%;
		margin: auto;
		margin-bottom: 117px;
		@include r(1920) {
			margin-bottom: 50px; }
		@include r(1300) {
			max-width: 750px;
			font-size: 50px;
			line-height: 100%; }
		@include r(768) {
			margin-bottom: 30px;
			font-size: 45px; }
		@include r(479) {
			font-size: 30px;
			margin-bottom: 16px; } }
	&__top {
		display: flex;
		align-items: flex-start;
		margin-bottom: 59px;
		@include r(1300) {
			margin-bottom: 63px; }
		@include r(900) {
			flex-wrap: wrap;
			margin-bottom: 20px;
			flex-direction: column; }
		@include r(640) {
			margin-bottom: 43px; } }

	&__desc {
		max-width: 360px;
		width: 100%;
		margin-right: 30px;
		font-size: 18.8px;
		line-height: 31px;
		color: $dark-blue;
		padding-top: 33px;
		border-top: 1px solid transparentize($light-purple, .5);
		@include r(1920) {
			max-width: 270px;
			margin-top: 35px;
			font-size: 18px;
			line-height: 30px;
			padding-top: 40px; }
		@include r(1300) {
			max-width: 220px;
			margin-top: 35px;
			font-size: 16px;
			line-height: 25px;
			padding-top: 40px; }
		@include r(900) {
			max-width: 100%;
			margin-top: 0;
			margin-right: 0;
			padding-top: 0;
			border: none;
			margin-bottom: 20px; } }
	&__left {
		margin-top: -55px;
		margin-left: 20px;
		position: relative;
		flex: 1;
		@include r(1920) {
			margin-top: 0; }
		@include r(1300) {
			margin-left: -32px; }
		@include r(900) {
			width: 100%;
			margin-left: 0; } }
	&__wrap {
		width: 720px;
		img {
			width: 100%;
			pointer-events: none;
			flex-shrink: 0; }
		@include r(1920) {
			width: 524px; }
		@include r(1024) {
			width: 70%; }
		@include r(640) {
			width: 100%; } }

	&__bottom {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		@include r(900) {
			flex-wrap: wrap; } }
	&__desc-list {
		background: #FFFFFF;
		box-shadow: 0px 35px 60px rgba(54, 44, 98, 0.2);
		border-radius: 10px;
		width: 400px;
		padding: 22px 32px 26px;
		position: absolute;
		top: 56px;
		right: 80px;
		min-height: 370px;
		display: flex;
		flex-direction: column;
		@include r(1920) {
			padding: 21px 31px 26px;
			top: 36px;
			right: 0;
			min-height: 360px; }
		@include r(1300) {
			width: 310px;
			top: 66px;
			padding: 21px 23px 26px;
			min-height: 340px; }
		@include r(1024) {
			width: 42%;
			top: 26px;
			min-height: 370px; }
		@include r(900) {
			width: 320px;
			top: 56px;
			min-height: 320px; }
		@include r(768) {
			width: 270px;
			top: 26px;
			min-height: 340px;
			padding: 18px; }
		@include r(640) {
			position: relative;
			width: 100%;
			top: auto;
			right: auto;
			margin-top: -50px;
			z-index: 2;
			min-height: auto;
			padding: 24px 18px 20px; } }
	&__dots {
		margin-top: auto;
		@include r(640) {
			margin-top: 20px; }
		@include r(479) {
			margin-top: 5px; } } }

.desc-map {
	display: none;
	&.active {
		display: block; }
	@include r(1024) {
		margin-bottom: 10px; }
	&__wrap-title {
		display: flex;
		margin-bottom: 10px;
		justify-content: space-between;
		@include r(1300) {
			margin-bottom: 7px; } }
	&__ico {
		margin-right: -6px;
		.fico {
			color: $purple;
			&-mercury {
				font-size: 34px; }
			&-venus {
				font-size: 30px; }
			&-moon {
				font-size: 25px; }
			&-saturn {
				font-size: 27px; }
			&-mars {
				font-size: 24px; }
			&-virgo2 {
				font-size: 23px; }
			@include nlt {
				margin-right: 8px;
				position: relative;
				top: 11px; } }
		@include r(1300) {
			margin-right: 0; } }
	&__top {
		margin-bottom: 15px;
		@include r(1300) {
			margin-bottom: 11px; } }
	&__title {
		font-size: 20px;
		line-height: 25px;
		max-width: 65%;
		min-height: 50px;
		@include r(1300) {
			font-size: 18px;
			line-height: 20px;
			min-height: 44px; }
		@include r(1024) {
			max-width: 78%; }
		@include r(640) {
			max-width: 50%; } }
	&__coordinates {
		color: $light-purple;
		font-size: 16px;
		line-height: 25px;
		@include r(1300) {
			font-size: 14px; } }
	&__bottom {
		margin-top: 15px; }
	&__text {
		color: $light-purple;
		font-style: italic;
		font-size: 16px;
		line-height: 25px;
		@include r(1300) {
			font-size: 14px;
			line-height: 20px; } } }

.progress {
	width: 100%;
	height: 1px;
	background-color: transparentize($purple, .8);
	position: relative;
	&__line {
		position: absolute;
		height: 1px;
		background: $purple;
		z-index: 1;
		width: 0%;
		transition: all 7s linear;
		&.part1 {
			width: 50%; }
		&.part2 {
			width: 100%; } } }

.dots {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	&--dark {
		li {
			border: 1px solid transparentize($purple, .6);
			&.active {
				border: 1px solid $purple; } } }
	&--light {
		li {
			border: 1px solid transparentize($white, .6);
			&.active {
				border: 1px solid $white; } } }
	&__item {
		width: 8px;
		height: 8px;
		border-radius: 50%;
		flex-shrink: 0;
		cursor: pointer;
		@include nlt {
			margin-right: 32px; }
		@include r(479) {
			@include nlt {
				margin-right: 22px; } } } }

.type {
	width: calc(100%/5 - 24px);
	@include r(1300) {
		width: calc(100%/5 - 17px); }
	@include r(900) {
		width: 100%;
		@include nlt {
			margin-bottom: 25px; } }
	@include r(768) {
		@include nlt {
			margin-bottom: 11px;
			padding-bottom: 11px;
			border-bottom: 1px solid transparentize(#000, .9); } }
	@include r(640) {
		@include nlt {
			margin-bottom: 9px;
			padding-bottom: 9px; } }
	&__top {
		padding-bottom: 21px;
		margin-bottom: 23px;
		border-bottom: 1px solid $purple;
		pointer-events: none;
		&.active {
			.fico-arrowFaq {
				transform: rotate(180deg); } }
		@include r(1300) {
			padding-bottom: 28px; }
		@include r(1024) {
			padding-bottom: 25px; }
		@include r(900) {
			display: flex;
			align-items: center;
			padding-bottom: 15px;
			margin-bottom: 15px; }
		@include r(768) {
			pointer-events: visible;
			padding-bottom: 0;
			margin-bottom: 0;
			border: none;
			cursor: pointer; } }

	&__title {
		line-height: 23px;
		@include r(1300) {
			font-size: 17px;
			line-height: 22px; }
		@include r(1024) {
			font-size: 16px; }
		@include r(900) {
			font-size: 17px;
			line-height: 18px;
			br {
				display: none; } } }

	&__wrap-ico {
		margin-bottom: 28px;
		@include r(1300) {
			margin-bottom: 31px; }
		@include r(900) {
			margin-bottom: 0;
			margin-right: 20px; }
		@include r(768) {
			width: 50px;
			text-align: center; }
		@include r(479) {
			width: 40px;
			flex-shrink: 0; } }
	.fico {
		color: $purple;
		font-size: 61px;
		&-arrowFaq {
			display: none; }
		@include r(1300) {
			font-size: 56px; }
		@include r(900) {
			font-size: 45px; }
		@include r(768) {
			font-size: 40px;
			&-arrowFaq {
				display: block;
				font-size: 11px;
				color: $dark-blue;
				margin-left: auto;
				transition: all .3s; } }
		@include r(640) {
			font-size: 35px;
			&-arrowFaq {
				font-size: 9px; } }
		@include r(479) {
			&-type1 {
				font-size: 30px; }
			&-type2 {
				font-size: 28px; }
			&-type3 {
				font-size: 31px; }
			&-type4 {
				font-size: 29px; }
			&-type5 {
				font-size: 29px; } } }
	&__bottom {
		display: block;
		@include r(768) {
			display: none;
			margin-top: 5px;
			margin-left: 70px; }
		@include r(479) {
			margin-left: 60px; } }
	&__text {
		color: $purple;
		max-width: 93%;
		@include r(1920) {
			max-width: 100%; } } }
