.menu-mobile {
	display: none;
	text-align: center;
	position: relative;
	padding: 0 20px;
	padding-top: 12px;
	padding-bottom: 35px;
	@include r(1024) {
		font-size: 20px;
		width: 100%;
		color: white;
		bottom: 0;
		display: block;
		position: fixed;
		right: 0;
		top: 0;
		z-index: 0;
		transform: translateX(100%);
		transition: all 1s;
		&--active {
			transform: translateX(0); } }
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
		height: 100%;
		overflow-y: auto;
		justify-content: space-between; }
	&__nav {
		margin: auto;
		display: flex;
		flex-direction: column;
		text-align: center;
		padding-top: 90px;
		.nav__item {
			font-size: 30px;
			@include nlt {
				margin-right: 0;
				margin-bottom: 19px; } } }
	&__bottom {
		margin-top: 30px;
		width: 100%;
		text-align: center; }
	&__btn {
		margin: auto;
		margin-bottom: 30px; }
	&__link {
		color: $dark-blue;
		text-decoration: none;
		margin-bottom: 10px;
		font-size: 20px;
		display: inline-block;
		margin-bottom: 23px;
		transition: all .3s;
		border-bottom: 1px solid transparentize($dark-blue, .999);
		&:hover {
			border-bottom: 1px solid $dark-blue; } }
	&__social {
		justify-content: center; } }
